import React, { Component } from 'react';
import './Footer.css';

class Footer extends Component {
    render() {
        return (
            <div className="footer" id="contact">
                <h2>{this.props.title}</h2>
                <p>Get in touch for further information</p>
                <div className="iconholder">
                    <script type="text/javascript" src="https://platform.linkedin.com/badges/js/profile.js" async defer></script>
                    <a href='https://uk.linkedin.com/in/penelopepoppycurtis?trk=profile-badge' target="_blank">
                        <img className="icon" src="data:image/svg+xml;base64," /></a>
                    <a href="https://github.com/PoppyCurtis" target="_blank">
                        <img className="icon" src="data:image/svg+xml;base64," /></a>
                    <a href='mailto:poppy.curtis@gmail.com' target="_blank">
                        <img className="icon" src="data:image/svg+xml;base64,"></img></a>
                </div>
                <footer> &copy; Copyright 2019. All Rights Reserved By Penelope Curtis</footer>
            </div>
        );
    }
}
export default Footer